<template>
  <div class="interview">
        <v-header @goback="goback" title="整件输机列表" back="主页"></v-header>
        <detail-list :list="list" @itemHandle="itemHandle"></detail-list>
        <router-view></router-view>
  </div>
</template>
<script>
import detailList from 'base/detail-list/detail-list';
import {getSJList} from 'api/api';
import vHeader from 'components/header/header';
import {mapMutations} from 'vuex';

const keys = ['checkState', 'catalogState', 'inputState'];

export default {
  data() {
    return {
      list: [],
      detail: false
    };
  },
  created() {
    this._getSJList();
  },
  methods: {
    _getSJList() {
      getSJList().then(res => {
        if (res.result) {
          res.data.forEach(item => {
            let states = item.extra;
            let currentKey = keys.find(key => {
              return !states[key].done;
            });
            item.currentState = states[currentKey].message;
          });
          this.list = res.data;
        }
      });
    },
    goback() {
      this.$router.push({path: '/bussiness'});
    },
    itemHandle(index) {
      let id = this.list[index].id;
      let customer = this.list[index];
      this.setCustomer(customer);
      switch(customer.currentState){
          case '未完成房查征信':
            this.$router.push({path: `/erWrite/check`});
            break;
          case '未填写资料目录表': 
            this.$router.push({path: `/erWrite/catalog`});
            break;
          case '待确定输机状态': 
            this.$router.push({path: `/erWrite/input`});
            break;
          default:
            this.goback()
      }
    },
    ...mapMutations({
      setCustomer: 'SET_CUSTOMER',
    }),
  },
  components: {
    detailList,
    vHeader,
  },
  watch:{
    $route(to, from) {
      let isChildrenRouter = /^\/erWrite\/.+$/.test(from.path);
      isChildrenRouter && this._getSJList();
    },
  }
};
</script>
<style lang="stylus" scoped>
</style>





